/* ---------------------------------- */
/* Forms Reset & Styles
------------------------------------- */

input,
input[type="text"],
input[type="password"],
input[type="email"],
input[type="number"],
textarea,
select {
	height: 51px;
	line-height: 51px;
	padding: 0 20px;
	outline: none;
	font-size: 15px;
	color: #808080;
	margin: 0 0 16px 0;
	max-width: 100%;
	width: 100%;
	box-sizing: border-box;
	display: block;
	background-color: #fff;
	border: 1px solid #dbdbdb;
	box-shadow: 0 1px 3px 0px rgba(0, 0, 0, 0.06);
	font-weight: 500;
	opacity: 1;
	border-radius: 3px;
}

input:-webkit-autofill:focus,
input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px #fff inset !important;
    -webkit-text-fill-color: #808080 !important;
}

select {
	padding: 15px 18px;
	cursor: pointer;
}

input {
	transition: all 0.1s ease-in-out;
}

input:focus,
input[type="text"]:focus,
input[type="password"]:focus,
input[type="email"]:focus,
input[type="number"]:focus,
textarea:focus {
	color: #808080;
	transition: box-shadow 0.2s !important;
	box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.07);
	border: 1px solid #d8d8d8;
	opacity: 1;
}

input[type="submit"] {
  border: none;
  padding: 11px 18px;
  width: auto;
}

input[type="checkbox"] { display: inline; }

input[type="radio"] {
	width: 15px;
	height: 15px;
	cursor: pointer;
	box-shadow: none;
}

/* Input Placeholder Color */
::-webkit-input-placeholder { /* WebKit browsers */
	color: #888;
	opacity: 1;
}

:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
	color: #888;
	opacity: 1;
}

::-moz-placeholder { /* Mozilla Firefox 19+ */
	color: #888;
	opacity: 1;
}

:-ms-input-placeholder { /* Internet Explorer 10+ */
	color: #888;
	opacity: 1;
}

textarea {
	height: auto;
	line-height: 27px;
	padding: 20px;
	min-height: 130px;
	transition: none !important;
	min-width: 100%;
}

label,
legend {
	display: block;
	font-weight: bold;
	font-size: 15px;
	font-weight: normal;
	margin-bottom: 8px;
}


label span,
legend span {
	font-weight: normal;
	font-size: 14px;
	color: #444;
}

fieldset {
    padding: 0;
    border: none;
}


a, button { outline: none !important; }
a:focus,
a:hover { text-decoration: none; color: #333;}
img { max-width: 100%; }


/* ---------------------------------- */
/* Header
------------------------------------- */
#logo { display: inline-block; margin-top: 0px; }

#logo img { max-height: 50px; width: auto; -webkit-transform: translate3d(0,0,0); transform: translate3d(0,0,0); }
img.footer-logo { max-height: 50px; width: auto; }

#header {
	/* padding-top:5px; */
	position: relative;
	z-index: 999;
	padding: 18px 0 8px 0;
	box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.12);
	font-size: 16px;
}

/* Header Widget */
.header-widget {
	position: relative;
	top: -1px;
	height: 54px;
}

.header-widget .button,
.header-widget .button.border {
	margin-right: 0;
	min-width: 130px;
	text-align: center;
	position: relative;
	line-height: 26px;
	font-weight: 500;
}

.header-widget .button.border i {
	font-size: 17px;
	padding: 0px 0 0 4px;
	top: 2px;
	display: inline-block;
	position: relative;
}

.header-widget .sign-in {
	position: relative;
	color: #333;
	margin-right: 25px;
	top: 10px;
	display: inline-block;
	vertical-align: top;
}

.header-widget .sign-in i { padding-right: 4px; font-size: 15px; }
.header-widget .sign-in:hover { color: #66676b; }


/* Full Width Header */
header.fullwidth .container {
	width: 100%;
	margin: 0;
	padding: 0 40px;
}

.left-side {
	float: left;
	width: 65%;
	display: inline-block;

}

.right-side {
	float: right;
	width: 35%;
	display: inline-block;
}

/* mobile fix */
@media (min-width: 1025px) and (max-width: 1239px)  {
	.right-side {
		width: auto;
		display: inline-block;
		margin-top: -126px;
		position: relative;
	}

	.left-side {
		width: 100%;
		display: inline-block;
	}
}

#logo,
#navigation { float: left; }

#logo { margin-right: 50px; }

.header-widget { text-align: right; }


/* ---------------------------------- */
/* Sticky Header
------------------------------------- */
#header.cloned {
	position: fixed;
	top: 0px;
	width: 100%;
	z-index: 999;
}

#header.cloned { background-color: #fff; }

/* Sticky header styles */
#header.cloned #logo img { -webkit-transform: translate3d(0,0,0); transform: translate3d(0,0,0); max-width: 120px; margin-top: 1px; }
#header.cloned { padding: 18px 0 7px 0; }

.sticky-logo { display: none; }

#header.cloned.unsticky {
	opacity: 1;
	visibility: hidden;
    pointer-events: none;
	-webkit-transform: translate(0,-100%) scale(1);
	        transform: translate(0,-100%) scale(1);
