.msps-container {
    position: absolute;
    height: 100%;
    margin: 0 auto;
    left: 0;
    right: 0;
    top: 0;
    z-index: 1;
}
 
.main-search-photo-slider {
    position: absolute;
    top: 0;
    right: 0;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0 3px 20px rgba(0,0,0,0.2);
    display: -webkit-flex;
    display: flex;
    top: 10%;
}
 
.msps-slider .slick-dots { bottom: 20px; z-index: 20; }
.msps-slider .slick-dots li {
    box-shadow: inset 0 0 0 2px #fff;
}
.msps-slider .slick-dots li.slick-active  {
    box-shadow: inset 0 0 0 6px #fff;
}
.msps-slider .slick-dots li:after {
    background-color: #fff;
}
 
 
.main-search-photo-slider img {
    object-fit: cover;
}
 
.msps-slider-container {
    position: relative;
/*  z-index: 10;*/
    display: -webkit-flex;
    display: flex;
}
 
/* Shapes */
.msps-shapes {
    position: absolute;
    top: 0;
    right: 0;
    top: 10%;
    -webkit-transform: none !important;
            transform: none !important;
    -webkit-transform-style: flat !important;
            transform-style: flat !important;
}
 
.msps-shapes .shape-a {
    position: absolute;
    right: -55px;
    top: 50px;
    z-index: 10;
}
 
.msps-shapes .shape-b {
    position: absolute;
    right: -40px;
    left: 0;
    margin: 0 auto;
    top: -35px;
    z-index: 10;
}
 
.msps-shapes .shape-c {
    position: absolute;
    left: -80px;
    bottom: 0;
    z-index: -10;
}
 
.msps-shapes .shape-d {
    position: absolute;
    right: -50px;
    bottom: 80px;
    z-index: -10;
}
 
.msps-shapes .shape-e {
    position: absolute;
    right: -30px;
    bottom: 55px;
    z-index: 10;
}
 
.msps-shapes .shape-f {
    position: absolute;
    left: -70px;
    top: 110px;
}
 
.shape-a.blurred { right: -90px; top: 8%; }
 
.shape-a,
.shape-b,
.shape-c,
.shape-d,
.shape-e,
.shape-f {
    opacity: 0;
    -webkit-transform: translateY(-25px);
            transform: translateY(-25px);
    transition: opacity 0.7s, -webkit-transform 0.7s;
    transition: opacity 0.7s, transform 0.7s;
    transition: opacity 0.7s, transform 0.7s, -webkit-transform 0.7s;
}
 
.shapes-animation .shape-a,
.shapes-animation .shape-b,
.shapes-animation .shape-c,
.shapes-animation .shape-d,
.shapes-animation .shape-e,
.shapes-animation .shape-f {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
 
.shapes-animation .shape-a { transition-delay: 1s; }
.shapes-animation .shape-b { transition-delay: 0.6s; }
.shapes-animation .shape-c { transition-delay: 0.4s; }
.shapes-animation .shape-d { transition-delay: 0.2s; }
.shapes-animation .shape-e { transition-delay: 0.6s; }
.shapes-animation .shape-f { transition-delay: 0.8s; }
 
@media (max-width: 1366px) {
 
    .msps-shapes .shape-d {
        position: absolute;
        right: -40px;
        bottom: 80px;
        z-index: -10;
    }
    .msps-shapes .shape-a {
        position: absolute;
        right: -20px;
        top: 50px;
        z-index: 10;
    }
}
 
 
.layer {
  width: 100%;
  height: 100%;
  z-index: 10;
}
 
.msps-slider .slick-slide .item {
    vertical-align: middle;
    object-fit: cover;
    width: 100%;
    height: 100%;
}
 
.msps-shapes,
.main-search-photo-slider,
.msps-slider .slick-slide,
.msps-slider {
    height: 600px;
    width: 530px;
}

