	border-radius: 4px;
	background-color: #f9f9f9;
	padding: 36px;
	padding-bottom: 40px;
	margin-bottom: 55px;
	margin-top: 70px;
	display: inline-block;
	width: 100%;
	position: relative;
}

.add-review-box h3 { margin-top: 5px }


.add-comment label span { color: #e24747; }

.add-comment input {
	margin: 0 0 25px 0;
	box-sizing: border-box;
}

.add-comment textarea,
.add-comment input {
	box-shadow: 0 1px 3px 0px rgba(0, 0, 0, 0.05)
}

.add-comment textarea {
	margin: 0 0 30px 0;
	width: 100%;
	min-width: 100%;
	min-height: 220px;
	resize: vertical;
	transition: none;
	width: 100%;
	box-sizing: border-box;
}

.add-comment .button { min-width: 160px; }


/* Leave Rating */
.leave-rating {
	height: 24px;
	float: left;
}

.leave-rating:hover input[type="radio"]:checked ~ label { color: #dadada; }

.leave-rating input[type="radio"] { display: none; }

.leave-rating input[type="radio"]:checked ~ label { color: #ffc600; }

.leave-rating label {
	font-size: 24px;
	float: right;
	letter-spacing: 4px;
	color: #dadada;
	cursor: pointer;
	transition: 0.3s;
}

.leave-rating label:hover,
.leave-rating label:hover ~ label {
	color: #ffc600 !important;
}

.leave-rating-title {
	display: block;
	margin: 35px 0 7px 0;
}


/* Upload Photos Button */
.add-review-photos {
	font-weight: 600;
	padding: 6px 7px;
	color: #666;
	background-color: #ededed;
	border-radius: 50px;
	display: inline-block;
	position: relative;
	font-size: 14px;
	width: auto;
	transition: 0.3s;
	cursor: pointer;
	line-height: 20px;
	float: right;
	top: -25px;
}

.photoUpload {
    position: relative;
    overflow: hidden;
    padding: 3px 12px;
	cursor: pointer;
}

.photoUpload input.upload {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    padding: 0;
    cursor: pointer;
    opacity: 0;
    filter: alpha(opacity=0);
}

.add-review-photos:hover {
	color: #fff;
	background-color: #66676b;
}

.add-review-photos i {
	color: #66676b;
	margin-right: 3px;
	transition: 0.3s;
	font-size: 16px;
	position: relative;
	top: 2px;
}

.add-review-photos:hover i { color: #fff; }

/* Helpfup Review */
a.rate-review {
	border: 1px solid #e0e0e0;
	border-radius: 50px;
	font-size: 13px;
	color: #666;
	font-weight: 500;
	padding: 5px 20px;
	margin-top: 20px;
	display: inline-block;
	transition: 0.3s;
}

a.rate-review span {
	border-left: 1px solid #e0e0e0;
	line-height: 20px;
	padding-left: 10px;
	margin-left: 5px;
	transition: 0.3s;
	color: #666;
}

a.rate-review i { padding-right: 3px; }

a.rate-review:hover span { border-color: #d0d0d0 }

a.rate-review:hover {
	border-color: #d0d0d0;
	background-color: #fafafa;
}


/* ---------------------------------- */
/* Pagination
------------------------------------- */
.pagination {
	margin: 9px 0 0 0;
	text-align: center;
	overflow: hidden;
}

.pagination-next-prev {
	position: relative;
	top: -66px;
}

.pagination ul li {
	display: inline-block;
	margin: 0;
	padding: 0;
}


.pagination ul li a,
.pagination-next-prev ul li a {
	padding: 12px 0;
	border-bottom: none;
	display: inline-block;
	color: #333;
	background-color: #fff;
	font-weight: 700;
	margin:0;
	line-height: 22px;
	transition: all 200ms ease-in-out;
	font-size: 14px;
}

.pagination ul li a {
	border-radius: 50%;
	width: 52px;
	height: 52px;
	padding: 0;
	line-height: 52px;
}

.pagination ul li a i {
	line-height: 52px;
}

.pagination ul li.blank {
	color: #a0a0a0;
	padding: 0 6px;
}

.pagination ul li a.current-page,
.pagination .current,
.pagination ul li a:hover,
.pagination-next-prev ul li a:hover {
	background-color: #f2f2f2;
	color: #333;
}

.pagination-next-prev a.next { float: right; }
.pagination-next-prev a.prev { float: left; }

.pagination-next-prev ul li a {
	color: #333;
	padding: 12px 16px;
	text-transform: uppercase;
}

.pagination ul,
.pagination-next-prev ul {	padding: 0; list-style-type: none;}


/* Listings Pagination */
.fs-listings .pagination ul li a,
.fs-listings .pagination-next-prev ul li a {
	background-color: transparent;
}

.fs-listings .pagination ul li a.current-page,
.fs-listings .pagination .current,
.fs-listings .pagination ul li a:hover,
.fs-listings .pagination-next-prev ul li a:hover {
	background-color: #e8e8e8;
	color: #333;
}


/* ---------------------------------- */
/* Contact Page
------------------------------------- */
#contact textarea {
	min-height: 200px;
	margin: 15px 0 25px 0;
}

#contact input {
	margin-bottom: 25px;
}

.loader { margin-left: 15px; }
.submit.disabled:hover,
.submit.disabled { background-color: #e8e8e8; color: #333; }

#contact input.button.submit {
	margin-bottom: 10px;
	line-height: 18px;
	height: 49px;
	transition: all 0.25s !important;
}


#contact input[type="submit"].submit:hover {
    opacity: 0.92;
}

.contact-sent {
	background-color: #EBF6E0;
	color: #5f9025;
	padding: 20px 26px;
	margin-bottom: 30px;
	border-radius: 3px;
}


/* Office Address Box */
.office-address {
	text-align: center;
	position: relative;
	color: #fff;
	display: table-cell;
	vertical-align: middle;
	height: 100%;
}

.office-address ul {
	list-style: none;
	font-size: 18px;
	padding: 0;
	line-height: 30px;
}

.office-address h3 {
	font-size: 28px;
	color: #fff;
	margin-top: 0;
}

.office-address h3:after {
	content: "";
	height: 2px;
	width: 50px;
	position: relative;
	display: block;
	background-color: #66676b;
	margin: 20px auto;
	border-radius: 2px;
}

.address-container {
	background-color: #2b2c30;
	background-size: 100%;
	background-position: 50% 50%;
	background-repeat: no-repeat;
	background-size: cover;
	display: table;
	width: 100%;
	position: relative;
	height: 100%
}

.address-container:before {
	content: "";
	height: 100%;
	width: 100%;
	position: absolute;
	top:0;
	left:0;
	display: block;
	background-color: #333;
	opacity: 0.7;
}


/* Google Maps */
.contact-map { display: -webkit-flex; display: flex; }
.contact-map #singleListingMap-container { -webkit-flex: 1 1; flex: 1 1; position: relative; padding-top: 0; }
.contact-map .address-box-container { -webkit-flex: 0 1 auto; flex: 0 1 auto; width: 440px; height: 450px; }
.contact-map #singleListingMap-container #singleListingMap { height: 100%; width: 100%; }

@media (min-width: 1680px) { .contact-map .address-box-container { height: 480px; } }
@media (max-width: 1440px) { .contact-map .address-box-container { height: 420px; } }


/* Contact Details */
.sidebar-textbox {
	display: inline-block;
	width: 100%;
	padding-right: 40px;
}

.sidebar-textbox.color {
	background-color: #666;
	color: #fff;
}

.sidebar-textbox.color h4 {color: #fff;}
.sidebar-textbox h4 {
	font-size: 16px;
	margin: 0;
	padding: 0;
	margin-bottom: 16px;
}

.sidebar-textbox span { color: #666; display: inline-block; }
.sidebar-textbox.color span { color: #fff; }

.sidebar-textbox ul.contact-details {
	list-style: none;
	padding: 5px 0 0 0;
}

.sidebar-textbox ul.contact-details strong {
	font-weight: 500;
}

.sidebar-textbox ul.contact-details li {
	text-align: left;
	padding-left: 60px;
	position: relative;
	width: 100%;
	display: inline-block;
	margin: 10px 0;
	line-height: 24px;
}

.sidebar-textbox ul.contact-details li a { color: #66676b; }

.sidebar-textbox ul.contact-details li strong {
	display: block;
	color: #333;
}

.sidebar-textbox ul.contact-details li i {
	height: 100%;
	position: absolute;
	left: 0;
	font-size: 32px;
	color: #333;
	top: 10px
}



/* ---------------------------------- */
/* Coming Soon Page
------------------------------------- */
.coming-soon-page {
	height: 100vh;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50%;
	position: relative;
}

.coming-soon-page .container {
	z-index: 15;
	position: relative;
	text-align: center;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
}


@media (max-width: 992px) {
	.coming-soon-page {
		min-height: 100vh;
		height: auto;
	}
	.coming-soon-page .container {
		padding-top: 50px;
		padding-bottom: 50px;
		top: auto;
		-webkit-transform: translateY(0%);
		        transform: translateY(0%);
	}
}


.coming-soon-page:before {
	height: 100%;
	width: 100%;
	position: absolute;
	z-index: 10;
	content: "";
	background-color: rgba(26,26,26,0.92)
}

.coming-soon-page h2,
.coming-soon-page h3 {
	margin: 0;
	padding: 0;
}

.coming-soon-page h2 {
	font-size: 46px;
	color: #fff;
}

.coming-soon-page h3 {
	font-size: 34px;
	font-weight: 300;
	margin: 30px 0 30px 0;
	color: rgba(255,255,255,0.9);
}

.coming-soon-page .main-search-input-item input {
	font-size: 16px;
	font-weight: 600;
}

.coming-soon-page .gray-style.main-search-input {
	max-width: 650px;
	margin: 0 auto;
}

.coming-soon-page .main-search-input button.button { font-size: 16px; }

/* Countdown */
#countdown {
	font-weight: 600;
	float: none;
	display: inline-block;
}

#countdown div {
	display: inline-block;
	margin: 0 5px;
	float: left;
}

#countdown div:first-child i { border: none;}

#countdown span {
	background-color: rgba(255,255,255,0.1);
	font-size: 40px;
	color: #fff;
	width: 120px;
	display: inline-block;
	padding: 32px 0;
	font-weight: 400;
	letter-spacing: -1px;
	border-radius: 3px 3px 0 0;
}

#countdown i {
	display: block;
	font-style: normal;
	margin: 0;
	text-transform: uppercase;
	font-size: 13px;
	padding: 4px 0;
	letter-spacing: 1px;
	background-color: #fff;
	color: #333;
	border-radius: 0 0 3px 3px;
}

.countdown-text {
	font-size: 24px;
	margin-top: 15px;
	line-height: 36px;
	font-weight: 300;
}

@media (max-width: 550px) {
	#countdown,
	#countdown div,
	#countdown span {
		width: 100%;
	}
	#countdown div {
		padding: 10px 0;
	}
}


/* ---------------------------------- */
/* Blog Styles
------------------------------------- */
.blog-post {
	box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
	background-color: #fff;
	display: inline-block;
	width: 100%;
	border-radius: 3px;
	margin: 15px 0;
}

section.fullwidth .blog-post { margin: 5px 0 0 0; }

.post-img {
	position: relative;
	height: 100%;
	width: 100%;
	display: block;
}

.post-img img {
	width: 100%;
	border-radius: 3px 3px 0 0;
}

.post-img:after {
	position: absolute;
	height: 100%;
	width: 100%;
	display: block;
	top: 0;
	left: 0;
	content: "";
	z-index: 9;
	opacity: 0.1;
	background: #333;
	transition: opacity 0.4s;
	border-radius: 3px 3px 0 0;
}

.post-img:hover:after {
	opacity: 0.5;
}


/* Post Hover Icon */
.post-img:before {
	opacity: 0;
	visibility: hidden;
	position: absolute;
	display: block;
	top: 50%;
	right: 0;
	left: 0;
	margin: 0 auto;
	text-align: center;
	content: "";
	z-index: 119;
	transition: 0.3s;
	-webkit-transform: translateY(-80%);
	        transform: translateY(-80%);
	width: 50px;
	height: 50px;
	text-align: center;
	border-radius: 50%;
	background-color: #66676b;
	background-image: url(/static/media/plus-icon.d77f0aed.svg);
	background-repeat: no-repeat;
	background-position: 50%
}

.post-img:hover:before {
	opacity: 1;
	visibility: visible;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
}


/* Content Styling */
.post-content { padding: 38px; }

.post-content p { margin: 18px 0 0 ; }

.post-content h3 {
	margin: 0;
	line-height: 34px;
	font-size: 22px;
}

.post-content h3 a { color: #333; }
.post-content h3 a:hover {color: #66676b; }

.post-content .meta-tags span {
	margin: 0 0 15px 0;
	display: inline-block;
	color: #888;
}

.post-content a.read-more {
	color: #66676b;
	margin: 23px 0 2px 0;
	display: inline-block;
}

a.read-more i {
	font-size: 20px;
	position: relative;
	padding-left: 4px;
	top: 1px;
	transition: 0.2s;
}

 a.read-more:hover i {
	padding-left: 7px;
}


/* Post Meta */
.post-meta {
	list-style: none;
	padding: 0;
	margin: 5px 0 0 0;
}

.post-meta li {
	display: inline-block;
	color: #888;
}

.post-meta li:after {
	content: "";
	width: 1px;
	height: 12px;
	background: #d0d0d0;
	display: inline-block;
	position: relative;
	top: 0px;
	margin: 0 10px;

}

.post-meta li:last-child:after { display: none; }

.post-meta li a { color: #888; }

.post-meta li a:hover {
	color: #66676b;
}


/* Blog Page Common Style */
.blog-page .blog-post {
	margin: 0 0 45px 0;
}

.blog-page h4.headline { font-size: 22px !important; font-weight: 400; }

/* Search Widget */
.search-blog-input { position: relative; }

.widget .search-blog-input .input:before {
	content: "\e090";
	font-size: 17px;
	color: #a0a0a0;
	position: absolute;
	display: inline-block;
	font-family: "simple-line-icons";
	right: 18px;
	top: 13px;
	margin-bottom: 0;
	background-color: #fff;
	padding-left: 7px;
}

#contact textarea,
#contact input,
.widget .search-blog-input input {
	transition: box-shadow 0.2s !important;
}

#contact textarea:focus,
#contact input:focus,
.widget .search-blog-input input:focus {
	transition: box-shadow 0.2s !important;
	box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.07);
}

/* Blog Widget Tabs */
.widget-tabs {
	padding: 0;
	list-style: none;
}

.widget-tabs li {
	padding: 0 0 27px 0;
	margin: 24px 0 0 0;
	border-bottom: 1px solid #e0e0e0;
}

.widget .tab-content { padding-bottom: 0; }

.widget-thumb a img {
	display: block;
	width: 95px;
	height: 95px;
	float: left;
	border-radius: 3px;
	margin: 3px 22px 0 0;
}

.widget-thumb {
	width: 85px;
	margin-right: 23px;
}

.widget-text h5 {
	font-size: 15px;
	line-height: 24px;
	margin: 0 0 5px 0;
}

.widget-text h5 a { color: #333; }
.widget-text h5 a:hover { color: #66676b }

.widget-text span {
	color: #888;
	font-weight: 400;
	line-height: 22px;
	margin: 0;
	display: block;
}

.widget-thumb {
	display: inline-block;
}

.widget-thumb a img {
	width: 85px;
	height: auto;
}

.widget-text {
	display: inline-block;
	width: 70%;
	padding: 0;
	position: relative;
	top: -2px;
}

.widget-tabs .widget-content {
	display: table;
}

.widget-tabs .widget-content .widget-text {
	display: table-cell;
	vertical-align: middle;
}

.widget-tabs li {
	padding: 0 0 20px 0;
	margin: 24px 0 0 0;
}

.widget-tabs li:first-child {
	padding: 0 0 20px 0;
	margin: 32px 0 0 0;
}

.widget-tabs li:last-child {
	border: none;
	padding-bottom: 0;
}

.widget h3 {
	margin-top: 0;
	margin-bottom: 25px;
	font-size: 22px;
}


/* Single Post */
.single-post p {
	margin: 20px 0;
}

.single-post p:last-child {
	margin-bottom: 0px;
}

.single-post .post-img {
	border-radius: 3px 3px 0 0;
}

.blog-page .blog-post .post-content,
.single-post .post-content {
	padding: 36px 43px;
}


/* About Author
------------------------------------- */
.about-author {
    margin: 10px 0 45px 0;
    padding: 30px 35px;
	background: #f8f8f8;
	border-radius: 4px;
}

.about-author p { margin: 5px 0 5px 0; }

.about-author a {
	position: relative;
	top: -7px;
	color: #66676b;
}

.about-author h4 {
	letter-spacing: 0;
}

.about-author img {
	float: left;
	margin: 7px 5px 5px 5px;
	width: 110px;
	height: 110px;
	border-radius: 50%;
}

.about-description {
	margin: 2px 0 0 150px;
}


/* ---------------------------------- */
/* Blog Compact Item
------------------------------------- */

.blog-compact-item-container {
	box-shadow: 0 0 7px 0 rgba(0, 0, 0, .12);
	display: block;
	height: 100%;
	width: 100%;
	border-radius: 4px;
	background: transparent;
	-webkit-transform: translate3d(0,0,0);
	        transform: translate3d(0,0,0);
	transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
	margin-bottom: 30px;
}

.blog-compact-item-container:hover {
	-webkit-transform: translate3d(0,-6px,0);
	        transform: translate3d(0,-6px,0);
}

.blog-compact-item {
	background: #ccc;
	border-radius: 4px;
	height: 100%;
	display: block;
	position: relative;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: 50%;
	height: 480px;
	z-index: 100;
	cursor: pointer;
}

.blog-compact-item img {
	object-fit: cover;
	height: 100%;
	width: 100%;
	border-radius: 4px;
}


.blog-compact-item:before {
	content: "";
	top: 0;
	position: absolute;
	height: 100%;
	width: 100%;
	z-index: 9;
	background: linear-gradient(to top, rgba(35,35,37,0.85) 0%, rgba(35,35,37,0.65) 35%, rgba(22,22,23,0) 60%, rgba(0,0,0,0) 100%);
/*	background-color: rgba(35,35,37,0.1);*/
	border-radius: 4px;
	opacity: 1;
}

.blog-compact-item-content {
	position: absolute;
	bottom: 32px;
	left: 0;
	padding: 0 34px;
	width: 100%;
	z-index: 50;
	box-sizing: border-box;
}

.blog-compact-item-content h3 {
	color: #fff;
	font-size: 20px;
	padding: 5px 0;
	font-weight: 500;
	margin: 2px 0 0 0;
	line-height: 31px;
}

.blog-compact-item-content span {
	font-size: 16px;
	font-weight: 300;
	display: inline-block;
	color: rgba(255,255,255,0.7);
}

.blog-compact-item-content p {
	font-size: 16px;
	font-weight: 300;
	display: inline-block;
	color: rgba(255,255,255,0.8);
	margin: 7px 0 0 0;
}

span.blog-item-tag {
	text-transform: uppercase;
	font-size: 9.7px;
	letter-spacing: 2.5px;
	font-weight: 60;
	background: #66676b;
	border-radius: 50px;
	padding: 5px 16px;
	line-height: 20px;
	color: #fff;
	font-weight: 400;
	margin-bottom: 9px;
	position: absolute;
	top: 30px;
	left: 32px;
	z-index: 110;
}

.blog-post-tags {
	list-style: none;
	padding: 0;
	margin: 0;
	margin-bottom: 0;
	color: #fff;
	font-size: 14px;
	font-weight: 400;
	opacity: 0.9;
}

.blog-post-tags li {
	display: inline-block;
	margin-right: 5px;
}

.blog-post-tags li i {
	margin-right: 3px;
	font-weight: normal;
	display: inline-block;
}



/* ------------------------------------------------------------------- */
/*  03. Shortcodes
---------------------------------------------------------------------- */

/* ---------------------------------- */
/* Buttons
------------------------------------- */

button.button,
input[type="button"],
input[type="submit"],
a.button.border,
a.button {
	background-color: #66676b;
	top: 0;
	padding: 9px 20px;
	color: #fff;
	position: relative;
	font-size: 15px;
	font-weight: 600;
	display: inline-block;
	transition: all 0.2s ease-in-out;
	cursor: pointer;
	margin-right: 6px;
	overflow: hidden;
	border: none;
	border-radius: 50px;
}


input[type="button"],
input[type="submit"] {
	line-height: 32px;
}

button.button:before,
a.button:before {
	width: 100%;
	height: 100%;
	content: "";
	display: block;
	background-color: #fff;
	position: absolute;
	left: 0;
	top: 0;
	opacity: 0;
	transition: all 0.2s;
}


button.button:hover:before,
a.button:hover:before {
	opacity: 0.1;
}

a.button.white {
	background-color: #fff;
	color: #333;
}

button.button { line-height: 26px; }

/* Border Button Style */
a.button.border:before { display: none; }

a.button.border {
	background-color: transparent;
	color: #66676b;
	border: 1px solid #66676b;
	padding: 9px 21px;
}

a.button.border:hover {
	background-color: #66676b;
	color: #fff;
	opacity: 1;
}

a.button.border.white {
