}

.listing-item-container.list-layout .listing-item-image:before {
	content: "";
	width: 100%;
	height: 100%;
	position: absolute;
	z-index: 1;
	display: block;
	background-color: #272729;
	opacity: 0.2;
}

.listing-item-container.list-layout span.like-icon {
	bottom: 50%;
	-webkit-transform: translateY(50%);
	        transform: translateY(50%);
